@import "_variable";
@import "_mixin";
header {
	position: relative; }
nav {
	position: fixed;
	width: 100%;
	height: 60px;
	background-color: $gray;
	z-index: 5; }
.menu {
    display: flex;
    justify-content: space-between;
    padding-top: 15px;
    margin: 0;
    list-style-type: none;
    &_link {
        font-weight: 400;
        font-size: 20px;
        text-decoration: none;
        color: $main_color;
        &:hover {
            font-weight: 600;
            color: $main_color; } } }

.promo {
    padding-top: 131px;
    padding-left: 5px;
    &_header {
        font-weight: 900;
        font-size: 60px;
        color: #000000; }
    &_subheader {
        margin-top: 48px;
        font-weight: 600;
        font-size: 25px;
        line-height: 30px;
        color: #000000; }
    &_btn {
        @include YellowBtn; }
    &_img {
        background: url(../img/Logo.png) center (center / cover) no-repeat;
        padding-left: 15px;
        width: 515px;
        height: 384px; } }
.about {
    margin-top: 150px;
    min-height: 1372px;
    padding: 0 15px;
    background: $main_color;
    padding-top: 28px;
    &_paragraph {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 25px; }
    &_img {
        width: 400px;
        height: 400px;
        border-radius: 100%; }
    &_descr {
        width: 495px;
        font-weight: 700;
        font-size: 48px;
        line-height: 59px;
        text-align: right;
        color: #000000;
        &:nth-child(1) {
            text-align: left; } } }
.catalog {
    width: 100%;
    background-color: $gray;
    padding-top: 72px;
    min-height: 728px;
    &_header {
        display: block;
        color: $main_color;
        margin: 0 auto;
        text-align: center;
        font-weight: 800;
        font-size: 40px;
        line-height: 49px; }
    &_item {
        margin: 0 auto;
        margin-top: 83px;
        padding-top: 171px;
        text-align: center;
        vertical-align: middle;
        color: #fff;
        height: 372px;
        width: 340px;
        font-weight: 700;
        font-size: 24px;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(../img/catalog1.png) center (center / cover) no-repeat;
        border: 7px solid #FECF00;
        &2 {
            background: linear-gradient(0deg, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(../img/catalog2.png) center (center / cover) no-repeat; }
        &3 {
            background: linear-gradient(0deg, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(../img/catalog3.png) center (center / cover) no-repeat; } } }
.services {
    background-color: $gray;
    min-height: 800px;
    padding-top: 53px;
    &_more {
        @include YellowBtn;
        margin: 0 auto;
        margin-top: 45px; }
    &_block {
        position: relative; }
    &_img {
        position: absolute;
        left: -250px;
        top: 20px;
        display: inline-block;
        width: 818px;
        height: 603px; }
    &_header {
        width: 100%;
        text-align: center;
        font-weight: 800;
        font-size: 48px;
        color: $main_color; }
    &_list {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding-top: 15px;
        margin: 0;
        list-style-type: none;
        &_item {
            position: relative;
            margin-top: 35px;
            &_title {
                display: inline-block;
                color: $main_color;
                font-weight: 600;
                font-size: 30px; }
            &_img {
                position: absolute;
                display: inline-block;
                width: 39px;
                height: 39px;
                left: -45px;
                top: 4px; } } } }
.tiles {
    padding-top: 60px;
    min-height: 1182px;
    &_title {
        font-weight: 800;
        font-size: 48px;
        line-height: 59px;
        color: #000000;
        margin: 0 auto;
        text-align: center; }
    &_header {
        margin-top: 34px;
        font-weight: 700;
        font-size: 24px;
        line-height: 29px;
        color: $main_color;
        text-align: center; }
    &_item {
        margin: 0 auto;
        margin-top: 21px;
        background: url(../img/1.png) center (center / cover) no-repeat;
        width: 360px;
        height: 360px;
        display: flex;
        align-items: flex-end;
        filter: drop-shadow(10px 10px 10px rgba(0, 0, 0, 0.25));
        border-radius: 37px;
        &_descr {
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            background: rgba(0, 0, 0, 0.5);
            width: 100%;
            height: 114px;
            border-bottom-left-radius: 37px;
            border-bottom-right-radius: 37px; }
        &_size {
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            color: #FFFFFF;
            text-align: center; }
        &_price {
            font-weight: 700;
            font-size: 16px;
            line-height: 20px;
            color: $main_color;
            text-align: center; }
        &_stying {
            font-weight: 700;
            font-size: 16px;
            line-height: 20px;
            color: $main_color;
            text-align: center; } }
    .tile_2 {
        background: url(../img/2.png) center (center / cover) no-repeat; }
    .tile_3 {
        background: url(../img/3.png) center (center / cover) no-repeat; }
    .tile_4 {
        background: url(../img/4.png) center (center / cover) no-repeat; }
    .tile_5 {
        background: url(../img/5.png) center (center / cover) no-repeat; }
    .tile_6 {
        background: url(../img/6.png) center (center / cover) no-repeat; } }





