@media (max-width: 1200px) {
    .promo_img {
        padding-left: 0px;
        width: 463px;
        height: 361px; }
    .catalog_item {
        width: 300px; }
    .services {
        &_img {
            left: -350px; }
        &_list_item {
            margin-top: 25px; } }
    .tiles_item {
        height: 300px;
        width: 300px;
        &_descr {
            height: 90px; }
        &_size {
            font-size: 14px; }
        &_price {
            font-size: 14px; }
        &_stying {
            font-size: 14px; } } }
@media (max-width: 992px) {
    .promo_img {
        width: 358px;
        height: 269px;
        margin-top: 50px; }
    .catalog {
        min-height: 600px;
        .catalog_item {
            height: 300px;
            width: 220px;
            font-weight: 700;
            font-size: 20px;
            padding-top: 127px; } }
    .about {
        min-height: 1100px;
        .about_img {
            width: 300px;
            height: 300px; }
        .about_descr {
            width: 350px;
            font-weight: 700;
            font-size: 30px;
            line-height: 36px; } }
    .services {
        &_img {
            display: none; } } }
@media (max-width: 767px) {
    .promo {
        &_header {
            text-align: center; }
        &_subheader {
            text-align: center; }
        &_img {
            display: none; }
        &_btn {
            display: block;
            margin: 0 auto;
            margin-top: 50px; } }
    .about {
        height: 1450px;
        margin-top: 80px;
        &_paragraph {
            margin-top: 60px;
            flex-direction: column; }
        &_descr {
            text-align: center;
            margin-top: 20px;
            &:nth-child(1) {
                text-align: center; } }
        ._change_order {
            order: -1; }
        ._change_width {
            width: 400px; } }
    .catalog {
        .catalog_change {
            height: 182px;
            width: 100%;
            padding-top: 74px; } }
    .tiles_item {
        height: 260px;
        width: 260px;
        &_descr {
            height: 70px; }
        &_size {
            font-size: 12px; }
        &_price {
            font-size: 12px; }
        &_stying {
            font-size: 12px; } } }

